import React from "react";
import {Link} from "react-router-dom";

function RComponent() {

    const componentStyle = {
        marginLeft: "auto",
        marginRight: "auto",
        backgroundColor: "white",
        color: "white",
        float: "right",
        width: "20vw",
        height: "70vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center"
    };

    const styleStart = {
        color:"white",
        borderColor: "black",
        borderWidth: 2,
        borderRadius: 100,
        borderStyle: 'solid',
        textDecoration: 'none',
        backgroundColor: "black",
    };

    return (
        <div style={componentStyle}>
            <Link style={styleStart} to="/"> </Link><br/><br/>
        </div>
    );
}

export default RComponent;
