import React, { useState } from 'react';
import Calendar from 'react-calendar';
import '../css/Records.css'
import { motion } from "framer-motion";
import moment from "moment";
import CalModal from './CalModal';
import DeleteModal from './DeleteModal';
import MiniModal from './MiniModal';
import ViewModal from './ViewModal';
import {useLocation, useNavigate} from "react-router-dom";
import axios from "axios";


export default function Records() {

    const calStyle = {
        width: '100vw',
        height: '100vh'
    };

    const [value, onChange] = useState(new Date());
    const [value2, onChange2] = useState(new Date());
    const [modalOpen, setModalOpen] = useState(false);
    const [modalViewOpen, setModalViewOpen] = useState(false);
    const [modalDeleteOpen, setModalDeleteOpen] = useState(false);
    const [miniModalOpen, setMiniModalOpen] = useState(false);

    const openModal = () => {
        setModalOpen(true);
    };
    const closeModal = () => {
        setModalOpen(false);
    };
    const openViewModal = () => {
        setModalViewOpen(true);
    };
    const closeViewModal = () => {
        setModalViewOpen(false);
    };
    const openModalDelete = () => {
        setModalDeleteOpen(true);
    };
    const closeModalDelete = () => {
        setModalDeleteOpen(false);
    };
    const openMiniModal = () => {
        setMiniModalOpen(true);
    };
    const closeMiniModal = () => {
        setMiniModalOpen(false);
    };

    const location = useLocation();
    const id = location.state.id;
    const img = location.state.img;

    const textStyle = {
        width: "100%",
        height: "250px",
        resize: "none"
    };

    const styleStart = {
        color:"white",
        borderColor: "black",
        borderWidth: 2,
        borderRadius: 100,
        borderStyle: 'solid',
        textDecoration: 'none',
        backgroundColor: "black",
    };

    const marks = [
        "15-11-2022",
        "03-11-2022",
        "07-11-2022",
        "12-11-2022",
        "13-11-2022",
        "15-11-2022",
    ];

    const navigate = useNavigate();

    const goToPrev = () => {
        navigate("/Contents", {state:{id, img}});
    };

    axios.get('/Records')
        .then((Response)=>{console.log(Response.data)})
        .catch((Error)=>{console.log(Error)});


    return (
        <motion.div initial={{opacity: 0}} animate={{opacity: 1}} exit={{opacity: 0}} transition={{ duration: 1 }}>
            <Calendar style={calStyle} onChange={onChange} value={value}
              tileClassName={({ date, view }) => {
                if (marks.find((x) => x === moment(date).format("DD-MM-YYYY"))) {
                    return "highlight";
                }
              }}
            />
            <button style={styleStart} onClick={openViewModal}>
                 
            </button>
            <button style={styleStart} onClick={openModal}>
                 
            </button>
            <button style={styleStart} onClick={openModalDelete}>
                 
            </button>
            <button style={styleStart} onClick={goToPrev}>
                 
            </button>

            <CalModal open={modalOpen} close={closeModal}>
                <main>
                     : <select>
                            <option value={id}>{id}</option>
                          </select>
                    <br/>
                      : <input readOnly value={moment(value).format("YYYY MM DD")}/>
                    <br/>
                      : <input/>
                    <br/>
                     
                    <textarea style={textStyle}>

                    </textarea>
                      : <input/>
                    <br/>
                      : <input onClick={openMiniModal} readOnly value={moment(value2).format("YYYY MM DD")}/>
                </main>
            </CalModal>
            <ViewModal open={modalViewOpen} close={closeViewModal}>
                <main>
                     : <select>
                    <option value={id}>{id}</option>
                </select>
                    <br/>
                      : <input readOnly value={moment(value).format("YYYY MM DD")}/>
                    <br/>
                      : <input readOnly/>
                    <br/>
                     
                    <textarea style={textStyle} readOnly>

                    </textarea>
                      : <input readOnly/>
                    <br/>
                      : <input readOnly/>
                </main>
            </ViewModal>
            <DeleteModal open={modalDeleteOpen} close={closeModalDelete} date={moment(value).format("YYYY MM DD")}/>
            <MiniModal open={miniModalOpen} close={closeMiniModal}>
                <Calendar style={calStyle} onChange={onChange2} value={value2}/>
            </MiniModal>
        </motion.div>
    );
}
