import React from 'react';
import { Link } from 'react-router-dom';

import { motion } from "framer-motion";

import foot from '../img/foot.png';

function Main() {
    const styleObj = {
        color: "black",
        textAlign:"center",
    };

    const styleFont = {
        fontSize: "100px",
        marginBottom: "0px",
        marginTop: "7%"
    };


    const styleStart = {
        fontSize: "87px",
        color:"white",
        borderColor: "black",
        borderWidth: 2,
        borderRadius: 100,
        borderStyle: 'solid',
        textDecoration: 'none',
        backgroundColor: "black",
        marginRight: "auto"
    };

    const rightImg = {
        marginLeft: "30px",
        position: "absolute"
    };

    return (
        <motion.div style={styleObj} initial={{opacity: 0}} animate={{opacity: 1}} exit={{opacity: 0}} transition={{ duration: 1 }}>
            <h1 style={styleFont}><br/><br/></h1>
            <Link style={styleStart} to="/choice">Start</Link>
            <motion.img src={foot} style={rightImg} initial={{opacity: 0}} animate={{opacity: 1}} exit={{opacity: 0}} transition={{ duration: 3 }}/>
            <br/>
        </motion.div>
    );
}

export default Main;