import React from "react";
import {Link} from "react-router-dom";

function TComponent(props) {

    const componentStyle = {
        marginLeft: "auto",
        marginRight: "auto",
        backgroundColor: "white",
        color: "white",
        height: "15vh",
        width: "100vw",
    };

    const styleStart = {
        color:"white",
        borderColor: "black",
        borderWidth: 2,
        borderRadius: 100,
        borderStyle: 'solid',
        textDecoration: 'none',
        backgroundColor: "black",
    };

    const styleOption = {
        color:"white",
        borderColor: "black",
        borderWidth: 2,
        borderRadius: 100,
        borderStyle: 'solid',
        textDecoration: 'none',
        backgroundColor: "black",
        float:"right"
    };

    return (
        <header style={componentStyle}>
            <Link style={styleStart} to="/"> </Link>
            <select>
                <option value={props.component}>{props.component}</option>
            </select>

            <Link style={styleOption} to="/"></Link>
            <Link style={styleOption} to="/"> </Link>
            <Link style={styleOption} to="/"></Link>
        </header>
    );
}

export default TComponent;
