import React, { useState } from 'react'; //Hooks  useState
import Modal from './Modal';
import SpaceModal from './SpaceModal';

function InputText(props) {
    //
    const style = {
        width: "300px",
        padding: '10px',
        margin: 'auto',
        /*border: '1px solid black'*/
    };

    const styleStart = {
        color:"white",
        borderColor: "black",
        borderWidth: 2,
        borderRadius: 100,
        borderStyle: 'solid',
        textDecoration: 'none',
        backgroundColor: "black",
        marginLeft: "10px"
    };

    //useState() -    
    const [id, setId] = useState(''); //  
    const [img, setImg] = useState(''); //  

    //state   - input   
    const onTextChange = (e) => {
        //e :  
        setId(e.target.value); //   value 
    };

    const [modalOpen, setModalOpen] = useState(false);
    const [spaceModalOpen, setSpaceModalOpen] = useState(false);

    const openModal = () => {
        if({id}.id === '' || {id}.id === ' '){
            setSpaceModalOpen(true);
        }else {
            let index = parseInt(document.getElementsByClassName('slick-current')[0].getAttribute("data-index"));
            setImg(index + 1);

            setModalOpen(true);
        }

    };
    const closeModal = () => {
        setModalOpen(false);
    };

    const closeSpaceModal = () => {
        setSpaceModalOpen(false);
    };

    const onKeyPress = (e) => {
        if(e.key === 'Enter') {
            openModal();
        }
    };

    return (
        <div style={ style }>
             : <input type="text" onKeyPress={onKeyPress} onChange={onTextChange} value={id}/>
            <button style={ styleStart } onClick={openModal}>
                
            </button>
            <Modal open={modalOpen} close={closeModal} id={ id } img={ img }>
                <main> { id } </main> ?
            </Modal>

            <SpaceModal open={spaceModalOpen} close={closeSpaceModal}>
                <main> !    ! </main>
            </SpaceModal>
        </div>
    );
}

export default InputText;